import React, {Component} from 'react'

export default class AboutMe extends Component {
  constructor(){
    super()
    this.state = {
      showResume: false
    }
    this.toggleShowResume = this.toggleShowResume.bind(this)
  }
  toggleShowResume(){
    this.setState({
      showResume: !this.state.showResume
    })
  }
  render() {
    return (
      <div className="about">
        <h4>Check out my blog!</h4>
        <a href="https://medium.com/@benfishernow"><img className="blog-pic" src="My_Blog.png" alt =""/></a>
        <hr/>
        <p className="flow-text">I am constantly looking for ways to make the world around me a better place. Software engineering is my freshest tool for doing just that. <br/><br/>I am looking to play my role in helping advance environmental awareness and having sustainable practices more easily available. Whether by saving my kitchen scraps for broth or developing an application that allows homeowners to estimate the cost of a photovoltaic system installation, I am here to help heal our world.</p>
        <hr/>
        <button className="resume-btn btn pulse" onClick={this.toggleShowResume}>See Ben's Resume</button>
        {this.state.showResume
        ? <object data="/resume.pdf" type="application/pdf" width="90%" height="600px" alt="Ben's Resume"/>
        : null
        }


      </div>
    )
  }
}
